@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@400;500;600&family=Josefin+Sans:wght@100;700&family=Outfit:wght@600;700&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Epilogue', sans-serif, 'Josefin Sans', sans-serif, 'Outfit', sans-serif;
}

:root {
    --mainColor: rgb(180, 149, 12);
    --mainColorLight: rgb(34, 92, 88);
    --secondaryColor: rgb(236, 152, 212);
    --textColor: #eee;
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 3rem;
    background: var(--mainColor);
    color: var(--textColor);
}

.logo-image {
    height: 4rem;
    border-radius: 50%;
}

nav a{
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
}

nav a:hover {
    color: var(--secondaryColor);
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

@media only screen and (max-width: 1024px) {
    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header nav {
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background: var(--mainColor);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive-nav {
        transform: none;
    }

    nav .nav-close-btn {
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a {
        font-size: 1.5rem;
    }
}